html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

#overview {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* Distributes items evenly */
  gap: 10px;
}

#overview .card {
  flex: 0 0 calc(33.33% - 10px); /* 33.33% width for three columns with a 20px gap */
  background-color: #efefef;
  padding: 10px;
  text-align: center;
}

#overview .card img,
#overview .card svg {
  max-width: 80%;
}

#overview .card svg {
  width: 100%;
  height: 100%;
}

#overview .card a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

#barCodeCard {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px 30px;
}

#barCodeDetailCard {
  max-width: 480px;
  width: 100%;
  text-align: center;
}

#barCodeDetailCard.bwipcard {
  max-width: 320px;
}

#barCodeDetailCard img,
#barCodeDetailCard svg {
  max-width: 100%;
}

#barCodeNumber {
  text-align: center;
  font-size: 28px;
  padding: 0 0 20px;
}

#barCodeDetailTitle {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin: 0 0 40px;
}

#barCodeDetailBackButton {
  width: 80%;
  margin: 20px auto 0;
}

#barCodeDetailBackButton a {
  display: block;
  text-align: center;
  color: white;
  background-color: darkcyan;
  border: 1px solid darkcyan;
  padding: 15px 30px;
  cursor: pointer;
  text-decoration: none;
  font-size: 20px;
}

#barCodeDetailBackButton a:hover {
  background-color: white;
  color: darkcyan;
}

#loginWrapper {
  width: 90%;
  margin: 20px auto;
  border: 1px solid #eee;
  border-radius: 4px;
  padding: 20px;
}

@media (max-width: 640px) {
  #overview .card {
    flex: 0 0 calc(50% - 10px) !important; /* 33.33% width for three columns with a 20px gap */
  }
}

